html, 
body {
  height: 100%;
  min-height: 100%;
  padding: 0;
}

#root {
  height: 100%;
  min-height: 100%;
}

.app {
  height: 100%;
  min-height: 100%;
}

.app__row {
  display: -ms-flexbox;
  display: flex;
}

.app__menu {}

.app__content {
  padding: 30px;
}

.app__menu-title {
  text-align: center;
  padding: 5px;
  color: #aaaaaa;
}

.app__tab-menu-item {
  position: relative;
  padding: 5px;
  min-width: 80px;
  border-radius: 10px 0 0 10px;
  border: 1px solid #cccccc;
  background-color: #f5f5f5;  
  cursor: pointer;
  margin-bottom: -1px; 
  margin-right: -1px;
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0, #f2f2f2), color-stop(50%, #fefefe));
  background-image: -webkit-linear-gradient(bottom, #f2f2f2 0, #fefefe 50%);
  background-image: -o-linear-gradient(bottom, #f2f2f2 0, #fefefe 50%);
  background-image: linear-gradient(to top, #f2f2f2 0, #fefefe 50%);
}

.app__tab-menu-item:hover {
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0, #f8f8f8), color-stop(50%, #ffffff));
  background-image: -webkit-linear-gradient(bottom, #f8f8f8 0, #ffffff 50%);
  background-image: -o-linear-gradient(bottom, #f8f8f8 0, #ffffff 50%);
  background-image: linear-gradient(to top, #f8f8f8 0, #ffffff 50%);
}

.app__tab-menu-item.is-active {
  border-right: none;
  background: #ffffff;
  z-index: 1;
}

.app__tab-symbol {
  text-align: center;
  font-size: 30px;
  font-weight: 700;
  text-shadow: -1px -1px 0 #ffffff;
  opacity: 0.6;
  line-height: 1;
}

.app__tab-menu-item.is-active .app__tab-symbol {
  text-align: center;
  font-size: 30px;
  font-weight: 700;
  opacity: 1;
}

.app__tab-label {
  font-size: 12px;
  line-height: 1;
  text-align: center;
}

.app__table {
  height: 100%;
  min-height: 100%;
}

.app__content-row {
  display: -ms-flexbox;
  display: flex;
}

.app__manager {
  width: 800px;
  max-width: 800px;
}

.app__conclusion {
  -ms-flex-positive: 2;
      flex-grow: 2;
  max-width: 400px;
}

.app__td--tab-menu {
  border-right: 1px solid #cccccc;
  background-color: #f5f5f5;
  padding: 0;
}



.app__status-box {
  min-height: 90%;
  border: 1px solid #cccccc;
  border-radius: 10px;
  padding: 30px;
  margin-top: 25px;
  line-height: 1.4;
  font-family: Calibri, Arial, serif;
  font-size: 12pt;
}

.list__item:nth-child(4n+1) {
   /*margin-bottom: 10px; */
}

.list__title {
  font-weight: 700;
  margin-top: 15px;
  margin-bottom: 3px;
}

.list__second {
  padding-left: 15px;
}

.list__box {
  margin-top: 10px;
}

.list__add {
  margin-top: 5px;
  margin-left: 15px;
}

.manager {
  display: -ms-flexbox;
  display: flex;
}

.manager__col {
  width: 50%;
}


